const _links = orderOfGroup.map( (source, index, order) => ({source, target: order[index < order.length - 1 ? index + 1 : 0] }) );

const linkByGroup = _nodes
  .filter(({id}) => id.includes(':') || id.includes('-'))
  .map(({id}) => ({source: id.split(id.includes(':') ? ':' : '-')[0], target:id}));


export const nodes =_nodes;
